import generateRequest from './services/request';

const request = generateRequest('Order');


export function getOrderList(condiotions) {
  return request('order_list', condiotions);
}

export function getOrderDetail(condiotions) {
  return request('order_detail', condiotions);
}

export function cancelOrder(condiotions) {
  return request('cancel_order', condiotions, { method: 'post' });
}

export function refundOrder(condiotions) { // 取消待发货的订单 这个取消是要退钱的
  return request('record_refund_order', condiotions, { method: 'post' });
}

export function confirmOrder(condiotions) {
  return request('order_confirm', condiotions, { method: 'post' });
}

export function addComment(condiotions) {
  return request('comment_add', condiotions, { method: 'post' });
}

export function getExpressDetail(condiotions) {
  return request('express', condiotions);
}

export function refundApply(condiotions) {
  return request('return_goods', condiotions, { method: 'post' });
}

export function getRefundDetail(condiotions) {
  return request('return_goods_info', condiotions);
}

export function getRefundList(condiotions) {
  return request('return_goods_list', condiotions);
}

export function cancelRefund(condiotions) {
  return request('return_goods_cancel', condiotions);
}

export function submitRefundExpress(condiotions) {
  return request('checkReturnInfo', condiotions, { method: 'post' });
}

export function confirmReceive(condiotions) {
  return request('receiveConfirm', condiotions);
}

export function getRefundAddress() {
  return request('return_goods_config');
}

