<template>
  <div class="refund-express">
    <fixed-header title="售后物流"></fixed-header>
    <div class="refund-express__main">
      <p class="refund-express__common">
        <span>物流公司</span>
        <input-item class="refund-express__common-input" v-model="delivery.express_name" type="text" placeholder="请填写物流公司"></input-item>
      </p>
      <p class="refund-express__common">
        <span>物流单号</span>
        <input-item class="refund-express__common-input" v-model="delivery.express_sn" type="number" placeholder="请填写物流单号"></input-item>
      </p>
    </div>
    <div class="refund-express__button">
      <button @click="submitRefundExpress">保存</button>
    </div>
  </div>
</template>
<script>
import InputItem from 'components/common/InputItem';
import FixedHeader from 'components/common/FixedHeader';
import * as orderApi from 'apis/order';

export default {
  components: {
    FixedHeader,
    InputItem,
  },
  data() {
    const { id } = this.$route.query;
    return {
      id,
      delivery: {
        express_name: '',
        express_sn: '',
      },
    };
  },
  methods: {
    async submitRefundExpress() {
      const { delivery: { express_name, express_sn } } = this;
      if (!express_name) {
        this.$toast('请填写物流公司');
        return;
      }
      if (!express_sn) {
        this.$toast('请填写物流单号');
        return;
      }
      try {
        await orderApi.submitRefundExpress({ delivery: this.delivery, id: this.id });
        this.$toast('提交成功');
        this.$router.go(-1);
      } catch (error) {
        this.$toast(`提交失败:${error.message}`);
      }
    },
  },
};
</script>
<style lang="scss">
  .refund-express {
    height: 100%;
    box-sizing: border-box;
    &__main {
      height: 100%;
      font-size: 28px;
    }
    &__common{
      display: flex;
      padding: 15px 30px;
      align-items: center;
      background: #fff;
      span {
        margin-right: 20px;
      }
      &-input {
        flex: 1;
        height: 40px;
        font-size: 28px;
      }
    }
    &__button{
      position: fixed;
      left: 0;
      right: 0;
      bottom: 0;
      padding: 20px 36px;
      button {
        padding: 20px 0;
        width: 100%;
        font-size: 30px;
        color: #ffffff;
        background-image: linear-gradient(90deg, #fa841b 17%, #ff6002 85%);
        border-radius: 12px;
      }
    }
  }
</style>
