<template>
  <div class="fixed-header">
    <button @click="goBack">
      <i v-if="hasGoBack" class="icon go_back"></i>
    </button>
    <div class="fixed-header__title">{{title}}</div>
    <div class="fixed-header__edit">
      <span v-if="isEdit" @click="edit">{{editTitle}}</span>
      <i v-if="isShare" class="icon share" @click="showShareModel"></i>
    </div>
  </div>
</template>
<script>
import MgtvApi from 'MgtvApi';
import { showShareModel, setShareContent } from 'utils';
import Env from 'utils/env';

export default {
  props: ['editTitle', 'title', 'shareContent', 'notShowBack', 'targetUrl'],
  computed: {
    hasGoBack() {
      return !this.notShowBack;
    },
    isApp() {
      return Env.isMgtvApp;
    },
    isShare() {
      return !!this.shareContent && this.isApp;
    },
    isEdit() {
      return !!this.editTitle;
    },
  },
  watch: {
    shareContent: {
      handler(val) {
        if (val) {
          setShareContent(val);
        }
      },
      immediate: true,
    },
  },
  beforeCreate() {
    MgtvApi.setWebviewTitle({ title: '芒果好物' });
  },
  methods: {
    showShareModel,
    edit() {
      this.$emit('edit');
    },
    goBack() {
      try {
        if (this.targetUrl) {
          this.$router.push({ path: this.targetUrl });
          return;
        }
        if (this.hasGoBack) {
          window.history.go(-1);
        }
      } catch (error) {
        this.$message(error.message);
      }
    },
  },


};
</script>

<style lang="scss" scoped>
.fixed-header {
  @media screen and (min-width: 900px) {
    width: 500px; /*no*/
    left: calc(50% - 250px); /*no*/
    right: calc(50% - 250px);/*no*/
    padding: 0;
  }
  position: fixed;
  z-index: 3000;
  background: #fff;
  top: 0;
  left: 0;
  right: 0;
  padding: 0 30px;
  display: flex;
  justify-content: space-around;
  div,
  button {
    flex: 1;
    height: 86px;
    padding-top: 12px;
    box-sizing: border-box;
    line-height: 86px;
  }
  button {
    text-align: left;
    padding-top: 15px;
  }
  &__title {
    font-size: 28px;
    color: #000;
    text-align: center;
  }
  &__edit {
    font-size: 30px;
    color: #9b9b9b;
    text-align: right;
  }
}
</style>
